@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/all.min.css);
.login-form{width:40rem;height:40rem;font-size:1.5rem;letter-spacing:1px;color:#f9f9f9;background-color:#2d445b;opacity:0.9;border-radius:1rem}.login-form h2{font-size:2rem;margin-top:1rem}.login-form input{width:20rem;font-size:1.2rem;border-radius:0.2rem;border:0.5px solid grey;text-align:center}.login-form button{width:10rem;height:3rem;border:none;border-radius:0.3rem;color:#2d445b;background-color:#f9f9f9}.login-form button:hover{color:#f9f9f9;background-color:#62c3f8}.login-form a{color:#62c3f8}.login-form a:hover{color:#f9f9f9}

.signup-form{width:40rem;height:50rem;font-size:1.5rem;letter-spacing:1px;color:#f9f9f9;background-color:#2d445b;opacity:0.8;border-radius:1rem}.signup-form h2{font-size:2rem;margin-top:1rem}.signup-form input{width:20rem;font-size:1.2rem;border-radius:0.2rem;border:0.5px solid grey;text-align:center}.signup-form button{width:15rem;height:3rem;margin-bottom:1rem;border:none;border-radius:0.3rem;color:#f9f9f9;background-color:#62c3f8}.signup-form button:hover{color:#2d445b;background-color:#f9f9f9}

h1{color:#2d445b}

ul{width:30%;list-style:none}ul a{border:none;text-decoration:none;background-color:transparent;font-size:1.5rem;letter-spacing:1px;list-style:none;color:#c4455a}ul button{font-size:1.5rem;letter-spacing:1px;border:none;border-radius:3px}

.toggle--button{font-size:1rem;text-align:center;cursor:pointer;display:table}.toggle--add{background-color:red}.toggle--delete{background-color:#4f83c7}.toggle--watched{background-color:#c536b2}.toggle--unwatched{background-color:#73c74f}

.stars-gray{position:relative;display:inline-block}.stars-yellow{position:absolute;top:0;left:0;white-space:nowrap;overflow:hidden;width:0}.stars-gray::before{content:"\f005 \f005 \f005 \f005 \f005";font-family:"Font Awesome 5 Free";font-weight:900;color:#ccc}.stars-yellow::before{content:"\f005 \f005 \f005 \f005 \f005";font-family:"Font Awesome 5 Free";font-weight:900;color:#fecf00}

.each-card{height:100%;cursor:pointer;background-color:#faf9f9}

.login-page{width:100vw;height:100vh;background-image:url(/cinema/static/media/pop.49b3c328.jpg);background-size:100%;background-position:100% 100%;background-repeat:no-repeat;background-attachment:fixed}

.signup-page{width:100vw;height:100vh;background:linear-gradient(80deg, #62C3F8, white)}

.react-paginate ul a{color:sandybrown}

.search-style{width:55%}.search-style input{height:3rem}.search-style .search-text{padding-top:1rem}

.goback-btn{width:7rem;height:2rem;border:none}

